<template>
  <div class="page page-earn-agreement">
    <div class="scrollable">
      <div class="title">{{ $root.title }}平台全程担保</div>
      <div class="title2">
        简单三步做商家，享受
        <span style="color: #ff9900">高额</span>
        补贴，轻松赚大钱
      </div>
      <div class="block">
        <div class="title">赚钱流程：</div>
        <div class="content">
          <van-steps
            direction="vertical"
            inactive-icon="arrow-down"
            active-icon="arrow-down"
            :active="3"
          >
            <van-step>
              <h3>一、开通成为商家</h3>
              <p>缴纳保证金可获取高额收益</p>
            </van-step>
            <van-step>
              <h3>二、充值数字货币</h3>
            </van-step>
            <van-step>
              <h3>三、转入商家余额</h3>
            </van-step>
            <van-step>
              <h3>四、变现拿高额补贴</h3>
            </van-step>
          </van-steps>
        </div>
      </div>
      <AgreementContent />
      <div class="whitespace"></div>
    </div>
    <div class="agreement">
      <van-checkbox v-model="checked" shape="square" checked-color="#ff0000">
        我已阅读
        <span>《赚钱规则》</span>，自愿成为
        {{ $root.title }}钱包商家，并自行判断和承担风险。
      </van-checkbox>
      <van-button type="primary" round block :disabled="!checked" @click="applyBusiness">
        确定成为商家
      </van-button>
    </div>
  </div>
</template>

<script lang="ts">
import Vue from "vue";
import { applyBusiness } from "@/apis/crypto";
import { verifySecuretedCode } from "@/vendor/globalVerify";
import AgreementContent from "./AgreementContent.vue";
export default Vue.extend({
  components: { AgreementContent },
  data() {
    return {
      checked: false,
    };
  },
  methods: {
    showAgreement() {
      this.$router.push("/earn_agreement_detail");
    },
    async applyBusiness() {
      const securePassword = await verifySecuretedCode(this);
      if (!securePassword) return;
      // 验证用户是否同意用户协议
      if (!this.checked) {
        this.$toast("请先同意用户协议");
        return;
      }
      const { ok, data, msg } = await applyBusiness({ securePassword });
      if (ok) {
        this.$toast("申请成功");
        this.$router.push("/earn");
        // 更改 userInfo 上的 BusinessStatus 为 1
        this.$root.userInfo.BusinessStatus = 1;
        // replace to /trade/earn
        this.$router.replace("/trade/earn");
      } else {
        this.$toast(msg);
      }
    },
  },
});
</script>

